* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "open sans", sans-serif;
}

div.App {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  flex-basis: 35vh;
  position: relative;
  overflow: hidden;
}

main {
  padding: 30px 0;
  display: flex;
  flex-grow: 1;
}

aside {
  min-width: 240px;
  background-color: #999;
}

section.blog {
  background-color: red;
  flex-grow: 1;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
}

.textArticle {
  margin-top: 10px;
  margin-bottom: 20px;
}

.products h2 {
  margin-bottom: 20px;
}

.products ul {
  list-style : square;
  padding-left: 30px;
}

.products li {
  margin-bottom: 20px;
}

.products a {
  text-decoration: none;
  color : #333;
}

.product a:hover {
  text-decoration: underline;
  color: black;
}

footer span{
  color:red;
}
footer p {
  font-size : 13px;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

ul {
  list-style-type: none;
}

nav.main {
  padding: 0 30px 30px 0;
}

nav.main a {
  display: block;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  transition: 0.2s;
}

nav.main a:hover {
  transform: translate(20px);
}

nav.main a.active {
  border-left: 10px gray solid;
  transform: translate(0);
}

nav.main li:nth-child(1) a.active {
  border-left-color: green;
}

nav.main li:nth-child(2) a.active {
  border-left-color: blue;
}

nav.main li:nth-child(3) a.active {
  border-left-color: red;
}

nav.main li:nth-child(4) a.active {
  border-left-color: orange;
}

.contact textarea {
  font-size: 16px;
  width: 80%;
  min-height: 200px;
  line-height: 1.6;
}

.contact button {
  display: block;
  border: 2px solid black;
  margin: 0 20% 0 auto;
  background-color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  transition: 0.2s;
}

.contact button:hover {
  color: yellow;
  background-color: black;
}

